footer {
  background-color: var(--primary-color);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
  a {
    color: var(--primary-bg);
  }
}

.footer {
  &__logo {
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
  }
  &__socials {
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
    a {
      background-color: var(--primary-bg);
      color: var(--color-white);
      padding: 0.8rem;
      border-radius: 0.7rem;
      display: flex;
      &:hover {
        background-color: transparent;
        color: var(--primary-bg);
      }
    }
  }
  &__copyright {
    margin-bottom: 4rem;
    color: var(--primary-bg);
  }
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}

/* ================ MEDIA QUERIES (Small Devices) ================== */

@media screen and (max-width: 600px) {
  .permalinks {
    flex-direction: column;
    gap: 1.5rem;
  }
  .footer {
    &__socials {
      margin-bottom: 2.6rem;
    }
  }
}
