.experience {
  &__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  &__frontend,
  &__backend {
    background-color: var(--various-bg);
    padding: 2.4rem 5rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    transition: var(--transition);
    &:hover {
      background-color: transparent;
    }
    h3 {
      text-align: center;
      margin-bottom: 2rem;
      color: var(--primary-color);
    }
  }
  &__content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 2rem;
  }
  &__details {
    display: flex;
    gap: 1rem;
    &-icon {
      margin-top: 6px;
      color: var(--primary-color);
    }
  }
}

/* ================ MEDIA QUERIES (Medium Devices) ================== */

@media screen and (max-width: 1024px) {
  .experience {
    &__container {
      grid-template-columns: 1fr;
    }
    &__frontend,
    &__backend {
      width: 80%;
      padding: 2rem;
      margin: 0 auto;
    }
    &__content {
      padding: 1rem;
    }
  }
}
/* ================ MEDIA QUERIES (Small Devices) ================== */

@media screen and (max-width: 600px) {
  .experience {
    &__container {
      gap: 1rem;
    }
    &__frontend,
    &__backend {
      width: 100%;
      padding: 2rem 1rem;
    }
  }
}
